-->
<template>
  <!-- 领料/退料管理列表 -->
  <el-dialog
    :class="[$store.state.dialogMarginClass]"
    width="90%"
    :modal="false"
    :title="programName + '单打印'"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    :show-close="false"
  >
    <div id="printDiv" class="content">
      <div class="printContent">
        <div class="printHead">
          <div class="printTitle">美康生物科技股份有限公司</div>
          <div class="printTitle">{{ orderData.documentHeader }}</div>
          <vue-barcode
            class="barCode"
            :value="orderData.documentNumber"
            :width="3.5"
            :height="15"
          >
          </vue-barcode>
        </div>
        <div class="printInfo">
          <el-row>
            <el-col class="infoL" :span="3">部门：</el-col>
            <el-col class="infoL" :span="5">{{ orderData.department }}</el-col>
            <el-col class="infoL" :span="3">领料类型：</el-col>
            <el-col class="infoL" :span="5">{{
              orderData.documentType
            }}</el-col>
            <el-col class="infoL" :span="3">单据编号：</el-col>
            <el-col class="infoL" :span="5">{{
              orderData.documentNumber
            }}</el-col>
          </el-row>
        </div>
        <div class="tableDiv">
          <table border="1" cellspacing="0" class="printTable">
            <thead>
              <tr>
                <th>行号</th>
                <th>备件代码</th>
                <th>备件名称</th>
                <th>规格型号</th>
                <th>英文简称</th>
                <th>单位</th>
                <th>申请数量</th>
                <th v-if="$store.state.programMkis === 1" >实发数量</th>
                <th v-if="$store.state.programMkis === 2" >实退数量</th>
                <th>SAP批次</th>
                <th>批号</th>
                <th>仓位</th>
                <th>有效期</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in orderList" :key="item.receiptId">
                <td>{{ item.lineNumber }}</td>
                <td>{{ item.materialsCode }}</td>
                <td>{{ item.materialsName }}</td>
                <td>{{ item.specifications }}</td>
                <td>{{ item.enAbbreviation }}</td>
                <td>{{ item.unit }}</td>
                <td>{{ formattrSq(item) }}</td>
                <td>{{ formattrSj(item) }}</td>
                <td>{{ item.sapBatch }}</td>
                <td>{{ item.sapBatch }}</td>
                <td>{{ item.position }}</td>
                <td>{{ formatterTime(item.validityPeriod) }}</td>
              </tr>
              <tr>
                <td>备注</td>
                <td colspan="5"></td>
                <td colspan="6">{{ allNum }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <el-row>
          <el-col class="infoX" :span="8">
            <p>制单人：{{ orderData.preparedBy }}</p>
            <p>制单日期：{{ formatterTime(orderData.preparedDate) }}</p>
          </el-col>
          <el-col class="infoX" :span="8">
            <p>{{ programName }}人：</p>
            <p>{{ programName }}日期：</p>
          </el-col>
          <el-col class="infoX" :span="8">
            <p>仓管员：</p>
            <p>发料日期：</p>
          </el-col>
        </el-row>
      </div>
    </div>
    <div slot="footer">
      <el-button type="primary" v-print="'#printDiv'">打印</el-button>
      <el-button type="tertiary" @click="closeAction">{{
        cancelLabel
      }}</el-button>
    </div>
  </el-dialog>
</template>
<script>

import { BTN_TEXT, CONSTANTS_CHECK, STATUS_CODE, TITLE } from '@/utils/constantsFile';
// 引入警告信息
import { showWarningMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
import VueBarcode from 'vue-barcode';
import {
  partsPickingGetPartPinkingMaterials,
  partsMaterialReturnGetPartsMaterialReturnMaterials
} from '@/api/mkConfig';
export default {
  name: 'CreateCheckOptionModal',
  components: {
    VueBarcode
  },
  props: {
    // 模态框标题
    title: { type: String, default: '' },
    // 取消按钮
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确定按钮
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 是否显示模态框
    showDialog: { type: Boolean, default: false },
    // 是否新增
    isAdd: { type: Boolean, default: true },
    // 模态框数据
    data: { type: Object, default: () => { } },
    // 备件功能名称
    programName: { type: String, default: '' },
    pageType: { type: String, default: '' },
    // 取消事件
    handleCancel: Function,
    // 保存事件
    handleConfirm: Function,
    // 关闭事件
    handlePrintClose: Function
  },
  data() {
    return {
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      // 点检项名称
      formUpdate: {
        itemName: ''
      },
      itemName: '',
      orderData: {},
      orderList: [],
      allNum: 0
    };
  },
  watch: {
    /**
     * @description: 监听点检项
     * @param val //当前值
     * @return: 无
     * @author: ZJM
     */
    data(val, oldVal) {
      if (this.data) {
        this.formUpdate.itemName = this.data.itemName;
        this.itemName = this.data.itemName;
      } else {
        this.formUpdate.itemName = '';
        this.itemName = '';
      }
    }
  },
  created() {
    // this.showBarCode();
  },
  methods: {
    async showData(row) {
      console.log(row);
      this.orderData = row;
      try {
        if (this.$store.state.programMkis === 1) {
          const res = await partsPickingGetPartPinkingMaterials({
            receiptId: row.receiptId
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.orderList = res.rows;

            this.allNum = 0;
            for (var i = 0; i < this.orderList.length; i++) {
              this.allNum += this.orderList[i].needCount;
            }
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } else {
          const res = await partsMaterialReturnGetPartsMaterialReturnMaterials({
            returnId: row.returnId
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.orderList = res.rows;

            this.allNum = 0;
            for (var x = 0; x < this.orderList.length; x++) {
              this.allNum += this.orderList[x].returnCount;
            }
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    formatterTime(val) {
      if (val) {
        return val.replace('T', ' ');
      } else {
        return '';
      }
    },
    formattrSq(row) {
      if (this.$store.state.programMkis === 1) {
        return row.needCount;
      } else {
        return row.returnCount;
      }
    },
    formattrSj(row) {
      if (this.$store.state.programMkis === 1) {
        return row.realCount;
      } else {
        return row.returnCount;
      }
    },
    /**
     * @description: 点击取消
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    cancelAction: () => {
      this.$refs.ruleForm.clearValidate();
      if (this.handleCancel) {
        this.handleCancel();
      }
    },
    /**
     * @description: 保存
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    confirmAction: () => {
      // if (this.handleConfirm) {
      // }
      this.$refs.ruleForm.validate(valid => {
        // 若验证通过
        if (valid) {
          var data = {
            isAdd: this.isAdd,
            data: this.formUpdate.itemName
          };
          if (!this.isAdd) {
            data.id = this.data.itemId;
          }
          this.handleConfirm(data);
          // this.clearData();
        }
      });
    },
    /**
     * @description: 点击关闭
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    closeAction: function() {
      // if (this.handlePrintClose) {
      //   this.handlePrintClose();
      // }
      this.$emit('handlePrintClose');
    }
  }
};
</script>
<style lang="less" scoped >
.content {
  text-align: center;
}
.printInfo {
  clear: both;
  p {
    line-height: normal;
    margin: 2px;
    height: 20px;
    text-align: left;
  }
  .infoL {
    text-align: left;
    padding-right: 20px;
    line-height: normal;
  }
}
.printTable {
  width: 100%;
  th {
    line-height: normal;
    font-weight: normal;
  }
  td {
    line-height: normal;
  }
}
.printHead {
  width: 100%;
  div {
    font-size: 25px;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
    line-height: normal;
  }
  div:nth-child(2) {
    letter-spacing: 25px;
  }
}
.barCode {
  /deep/ text {
    display: none;
  }
  /deep/ svg {
    height: 47px;
  }
  /deep/ rect {
    height: 37px;
  }
}
.tableDiv {
  min-height: 300px;
}
.infoX {
  margin-top: 10px;
  P {
    margin: 0;
    line-height: normal;
    text-align: left;
  }
}
</style>
