// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
import {
  insertOrUpdateRepairTypeBatch
} from '@/api/equipment';
// 引入通用方法
import { CommonService } from '../../../../services/common.service';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { TITLE, STATUS_CODE, MSG, VARIABLE, BTN_TEXT, EQUIPMENT_DATA } from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem
  },
  props: {
  },
  data() {
    // 验证电话
    const selectValid = ({ cellValue }) => {
      return new Promise((resolve, reject) => {
        if (cellValue.length === 0) {
          reject(new Error(MSG.PLEASE_CHOOSE_CONTENT));
        } else {
          resolve();
        }
      });
    };

    return {
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      MSG: MSG,
      imageUrl: '',
      // 页面标题
      titleName: EQUIPMENT_DATA.ADD_REPAIR_TYPE,
      // 表单校验规则
      validRules: {
        repairName: [
          {
            required: true,
            message: MSG.PLEASE_ENTER_CONTENT
          },
          { max: 100, message: '长度不得大于100个字符' },
          { pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/, message: '不允许输入空格等特殊符号' }
        ],
        typeIdList: [
          { validator: selectValid }
        ]
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'repairName',
          title: EQUIPMENT_DATA.REPAIRTYPE_NM,
          minWidth: '140',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'typeIdList',
          title: EQUIPMENT_DATA.LINK_TOOL_TYPE,
          minWidth: '140',
          align: 'center',
          editRender: {
            name: 'ElSelect',
            options: [],
            props: { multiple: true }
          }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '100',
          fixed: 'right',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <i
                  onClick={() => this.copyRow(row, column)}
                  class="icon-table-addRow"
                ></i>,
                <i
                  onClick={() => this.deleteRow(row, column)}
                  class="icon-table-delete"
                ></i>
              ];
            }
          }
        }
      ],
      devType: []
    };
  },
  computed: {},
  created() {
    CommonService.getRelatedDeviceType();
    setTimeout(() => {
      this.init();
    }, 300);
  },
  mounted() {
  },
  methods: {
    async init() {
      var arr = this.$store.state.filters.RelatedDeviceType;
      for (var i = 0; i < arr.length; i++) {
        arr[i].label = arr[i].typeName;
        arr[i].value = arr[i].typeId;
      }
      this.tableColumns[1].editRender.options = arr;
      this.insertEvent();
    },
    async insertEvent() {
      // 默认新增一行
      var record = {
        repairName: '',
        typeIdList: []
      };
      await this.$refs.xGrid.insertAt(record, -1);
    },
    /**
     * @description: 移除一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    deleteRow(row, column) {
      this.$XModal.confirm(MSG.CONFIRM_DELETE).then(type => {
        if (type === 'confirm') {
          this.$refs.xGrid.remove(row);
        }
      });
    },
    async copyRow(row, column) {
      this.$refs.xGrid.clearActived();
      // const index = this.$refs.xGrid.$getRowIndex(row);
      const record = { ...row };
      delete record._XID;
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.xGrid.insertAt(record, row);
    },
    /**
     * @description: 表单提交
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submit() {
      try {
        const {
          // 新增的数据
          insertRecords
        } = this.$refs.xGrid.getRecordset();

        const data = JSON.parse(JSON.stringify(insertRecords));
        const res = await insertOrUpdateRepairTypeBatch(
          {},
          data);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.goBack();
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行验证
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async fullValidEvent() {
      const errMap = await this.$refs.xGrid
        .fullValidate()
        .catch(errMap => errMap);
      if (errMap) {
        var msgList = [];
        Object.values(errMap).forEach(errList => {
          errList.forEach((params, index) => {
            const { column, rules } = params;
            rules.forEach(rule => {
              msgList.push(
                `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} ${column.title} ${MSG.VALIDATION_FAILED}：${rule.message}`
              );
            });
          });
        });
        this.$XModal.alert({
          status: 'error',
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                {msgList.map(msg => (
                  <div>{msg}</div>
                ))}
              </div>
            ];
          }
        });
      } else {
        // 通过验证，执行提交
        this.submit();
      }
    },
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    goBack() {
      this.$emit('goBack', VARIABLE.ZERO_NUMBER);
    }
  }
};
