<template>
  <div>
    <!-- 领料/退料管理列表 -->
    <el-dialog
      :class="[$store.state.dialogMarginClass]"
      width="80%"
      :modal="false"
      :title="'新增'+programName+'单'"
      :visible.sync="showDialog"
      :close-on-click-modal="false"
      :show-close="false"
    >
      <div class="content">
          <vxe-grid
            border="full"
            resizable
            keep-source
            ref="xGrid"
            class="vxe-table-element"
            height="350"
            highlight-current-row
            :radio-config="{trigger: 'row'}"
            :columns="tableColumns"
            :data="tableDatas"
            :edit-config="{trigger: 'click', mode: 'cell', showStatus: false, icon: 'fa fa-file-text-o'}"
          ></vxe-grid>
      </div>
      <div slot="footer">
        <el-button type="primary" @click="saveAction">保存</el-button>
        <el-button type="tertiary" @click="cancelAction">{{cancelLabel}}</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
// 美康领料退料组件
import { MSG, BTN_TEXT, CONSTANTS_CHECK, STATUS_CODE, TITLE } from '@/utils/constantsFile';
// 引入警告信息
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
import {
  partsPickingGetAllPartPinkingMaterialsByRelationshipId,
  partsMaterialReturnCreatePartsMaterialReturn
} from '@/api/mkConfig';
export default {
  name: 'CreateCheckOptionModal',
  components: {
  },
  props: {
    // 模态框标题
    title: { type: String, default: '' },
    // 取消按钮
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确定按钮
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 是否显示模态框
    showDialog: { type: Boolean, default: false },
    // 是否新增
    isAdd: { type: Boolean, default: true },
    // 模态框数据
    data: { type: Object, default: () => {} },
    // 备件功能名称
    programName: { type: String, default: '' },
    pageType: { type: String, default: '' },
    // 取消事件
    handleCancel: Function,
    // 保存事件
    handleConfirm: Function,
    // 关闭事件
    handleClose: Function
  },
  data() {
    return {
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      // 点检项名称
      formUpdate: {
        itemName: ''
      },
      itemName: '',
      rules: {
        itemName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT, trigger: 'blur' },
          { max: 100, message: '长度不得大于100个字符' }
        ]
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'materialsCode',
          title: '备件编码',
          align: 'center'
        },
        {
          field: 'materialsName',
          title: '备件名称',
          align: 'center'
        },
        {
          field: 'specifications',
          title: '规格型号',
          align: 'center'
        },
        {
          field: 'sapBatch',
          title: '批次',
          align: 'center'
        },
        {
          field: 'position',
          title: '库位',
          align: 'center'
        },
        {
          field: 'realCount',
          title: '已申请数量',
          align: 'center'
        },
        {
          field: 'returnCount',
          title: '数量',
          align: 'center',
          editRender: { name: 'input' }
        }
      ],
      showPrintDialog: false
    };
  },
  watch: {
    /**
     * @description: 监听点检项
     * @param val //当前值
     * @return: 无
     * @author: ZJM
     */
    data(val, oldVal) {
      if (this.data) {
        this.formUpdate.itemName = this.data.itemName;
        this.itemName = this.data.itemName;
      } else {
        this.formUpdate.itemName = '';
        this.itemName = '';
      }
    }
  },
  methods: {
    async showList() {
      this.tableDatas = [];
      var relationshipId = 0;
      var relationshipType = 0;
      if (this.$store.state.materialMkis === 1) { // 维修
        relationshipType = 1;
        relationshipId = this.$store.state.materialRepair.repairTaskId;
      } else { // 保养
        relationshipType = 2;
        relationshipId = this.$store.state.materialMaintance.maintainId;
      }

      try {
        const res = await partsPickingGetAllPartPinkingMaterialsByRelationshipId({
          relationshipId: relationshipId,
          relationshipType: relationshipType
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(res.rows);
          this.tableDatas = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 点击取消
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    cancelAction: function() {
      // this.$refs.ruleForm.clearValidate();
      // if (this.handlePrintClose) {
      //   this.handleCancel();
      // }
      this.$emit('handlePrintClose');
    },
    async saveAction() {
      var deviceCode = '';
      var relationshipId = 0;
      var relationshipType = 0;
      if (this.$store.state.materialMkis === 1) { // 维修
        relationshipType = 1;
        deviceCode = this.$store.state.materialRepair.deviceCode;
        relationshipId = this.$store.state.materialRepair.repairTaskId;
      } else { // 保养
        relationshipType = 2;
        deviceCode = this.$store.state.materialMaintance.deviceCode;
        relationshipId = this.$store.state.materialMaintance.maintainId;
      }

      var paramJson = {
        deviceCode: deviceCode,
        partsMaterialReturnMaterialsList: [],
        relationshipId: relationshipId,
        relationshipType: relationshipType,
        remark: ''
      };
      console.log(paramJson);
      console.log(this.tableDatas);
      var jsonArr = [];
      for (var i = 0; i < this.tableDatas.length; i++) {
        if (this.tableDatas[i].returnCount && !isNaN(Number(this.tableDatas[i].returnCount))) {
          if (Number(this.tableDatas[i].returnCount) > 0 && Number(this.tableDatas[i].returnCount) <= Number(this.tableDatas[i].realCount)) {
            jsonArr.push(this.tableDatas[i]);
          }
        }
      }

      if (jsonArr.length === 0) {
        showWarningMessage('请检查数据是否超出上限或填入不可用数据');
        return false;
      } else {
        paramJson.partsMaterialReturnMaterialsList = jsonArr;
      }

      try {
        const res = await partsMaterialReturnCreatePartsMaterialReturn({}, paramJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.cancelAction();
        } else {
          // 提示异常信息
          showWarningMessage(res.dataset);
        }
      } catch (error) {
        this.tableLoading = false;
        // showErrorNotify(TITLE.NOTIFY, '无数据');
      } finally {
      }
    },
    /**
     * @description: 保存
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    confirmAction: function() {
      // if (this.handleConfirm) {
      // }
      this.$refs.ruleForm.validate(valid => {
        // 若验证通过
        if (valid) {
          var data = {
            isAdd: this.isAdd,
            data: this.formUpdate.itemName
          };
          if (!this.isAdd) {
            data.id = this.data.itemId;
          }
          this.handleConfirm(data);
          // this.clearData();
        }
      });
    },
    /**
     * @description: 点击关闭
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    closeAction: function() {
      this.$refs.ruleForm.clearValidate();
      if (this.handleClose) {
        this.handleClose();
      }
    }
  }
};
</script>
