 * @Descripttion: 备件管理
 * @version:
 * @Author: ZJM
 * @Date: 2020-06-17 09:41:09
 * @LastEditors: ZJM
 * @LastEditTime: 2022-07-15 11:13:53
-->
<template>
  <div>
    <!-- 领料/退料管理列表 -->
    <el-dialog
      :class="[$store.state.dialogMarginClass]"
      width="800px"
      :modal="true"
      :close-on-click-modal="false"
      :show-close="false"
      :title="programName+'单管理'"
      :visible.sync="showDialog"
    >
      <div class="content" style="text-align: left;" >
        <el-button type="primary" size="mini" @click="showAdd"
          >新增</el-button
        >
        <vxe-grid
          border="full"
          resizable
          keep-source
          ref="xGrid"
          class="vxe-table-element"
          height="350"
          highlight-current-row
          :radio-config="{ trigger: 'row' }"
          :columns="tableColumns"
          :data="tableDatas"
          :edit-config="{
            trigger: 'click',
            mode: 'cell',
            showStatus: false,
            icon: 'fa fa-file-text-o',
          }"
        ></vxe-grid>
      </div>
      <div slot="footer">
        <el-button type="tertiary" @click="cancelAction">{{
          cancelLabel
        }}</el-button>
      </div>
    </el-dialog>

    <printModal
      ref="printModal"
      :pageType="pageType"
      :programName="programName"
      :showDialog="showPrintDialog"
      @handlePrintClose="handlePrintClose"
    ></printModal>

    <addReturnMaterialModal
      ref="addReturnMaterial"
      :pageType="pageType"
      :programName="programName"
      :showDialog="showAddReturnMaterialDialog"
      @handlePrintClose="handlePrintClose"
    ></addReturnMaterialModal>

    <pickModal
      ref="pickModal"
      :pageType="pageType"
      :programName="programName"
      :showDialog="showPickDialog"
      @handlePrintClose="handlePrintClose"
    ></pickModal>
  </div>
</template>
<script>
// 美康领料退料组件
import printModal from '@/components/modal/materialMkModal/printModal.vue';
// 新增退料
import addReturnMaterialModal from '@/components/modal/materialMkModal/addReturnMaterialModal.vue';
// 引入警告信息
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
// 新增领料
import pickModal from '@/components/modal/materialMkModal/pickModal';
import { MSG, BTN_TEXT, CONSTANTS_CHECK, STATUS_CODE, TITLE } from '@/utils/constantsFile';
import {
  partsPickingGetPartPinking,
  partsPickingRemovePartPinking,
  partsMaterialReturnRemovePartsMaterialReturn,
  partsMaterialReturnGetPartsMaterialReturn
} from '@/api/mkConfig';
export default {
  name: 'CreateCheckOptionModal',
  components: {
    printModal,
    addReturnMaterialModal,
    pickModal
  },
  props: {
    // 模态框标题
    title: { type: String, default: '' },
    // 取消按钮
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确定按钮
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 是否显示模态框
    showDialog: { type: Boolean, default: false },
    // 是否新增
    isAdd: { type: Boolean, default: true },
    // 模态框数据
    data: { type: Object, default: () => { } },
    // 备件功能名称
    programName: { type: String, default: '' },
    pageType: { type: String, default: '' },
    // 功能编号 1领料  2退料
    programNum: { type: Number, default: 1 },
    // 取消事件
    handleCancel: Function,
    // 保存事件
    handleConfirm: Function,
    // 关闭事件
    handleClose: Function
  },
  data() {
    return {
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      // 点检项名称
      formUpdate: {
        itemName: ''
      },
      itemName: '',
      rules: {
        itemName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT, trigger: 'blur' },
          { max: 100, message: '长度不得大于100个字符' }
        ]
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'documentNumber',
          title: '',
          align: 'center'
        },
        {
          field: 'preparedDate',
          title: '制单日期',
          align: 'center',
          formatter({ cellValue, row, column }) {
            if (cellValue) {
              return cellValue.replace('T', ' ');
            } else {
              return '';
            }
          }
        },
        {
          field: 'documentState',
          title: '单据状态',
          align: 'center',
          slots: {
            default: ({ row, column, cellValue }) => {
              if (row.documentState === 1) {
                return [
                  <span style="color:orange;" >已申请</span>
                ];
              } else if (row.documentState === 2) {
                if (this.$store.state.programMkis === 1) {
                  return [
                    <span style="color:green;" >已领</span>
                  ];
                } else {
                  return [
                    <span style="color:green;" >已退</span>
                  ];
                }
              } else if (row.documentState === 3) {
                return [
                  <span style="color:red;" >已作废</span>
                ];
              }
            }
          }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '',
          align: 'center',
          // type: 'html',
          slots: {
            default: ({ row, column, cellValue }) => {
              if (row.documentState === 3) {
                return [
                  <div>
                    <el-button
                      type="primary"
                      size="mini"
                      onClick={() => this.showPrint(row, column, false)}
                    >
                      打印
                    </el-button>
                  </div>
                ];
              } else {
                return [
                  <div>
                    <el-button
                      type="danger"
                      size="mini"
                      onClick={() => this.delete(row, column, false)}
                    >
                      作废
                    </el-button>
                    <el-button
                      type="primary"
                      size="mini"
                      onClick={() => this.showPrint(row, column, false)}
                    >
                      打印
                    </el-button>
                  </div>
                ];
              }
            }
          }
        }
      ],
      showPrintDialog: false,
      showAddReturnMaterialDialog: false,
      hideMaterial: false,
      showPickDialog: false,
      programNamex: '',
      relationshipId: 0
    };
  },
  created() {
    // this.showList();
  },
  watch: {
    /**
     * @description: 监听点检项
     * @param val //当前值
     * @return: 无
     * @author: ZJM
     */
    data(val, oldVal) {
      if (this.data) {
        this.formUpdate.itemName = this.data.itemName;
        this.itemName = this.data.itemName;
      } else {
        this.formUpdate.itemName = '';
        this.itemName = '';
      }
    }
  },
  methods: {
    async showList() {
      this.tableDatas = [];
      var relationshipId = 0;
      var relationshipType = 0;
      if (this.$store.state.materialMkis === 1) { // 维修
        relationshipType = 1;
        relationshipId = this.$store.state.materialRepair.repairTaskId;
      } else { // 保养
        relationshipType = 2;
        relationshipId = this.$store.state.materialMaintance.maintainId;
      }

      try {
        if (this.$store.state.programMkis === 1) {
          const res = await partsPickingGetPartPinking({
            relationshipId: relationshipId,
            relationshipType: relationshipType,
            current: 1,
            size: 99999999
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            console.log(res.rows);
            this.tableDatas = res.rows.reverse();
          } else {
            // 提示异常信息
            // showWarningMessage(res.message);
          }
        } else {
          const res = await partsMaterialReturnGetPartsMaterialReturn({
            relationshipId: relationshipId,
            relationshipType: relationshipType,
            current: 1,
            size: 99999999
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            console.log(res.rows);
            this.tableDatas = res.rows.reverse();
          } else {
            // 提示异常信息
            // showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    titNameFunc(param) {
      this.tableColumns[0].title = param + '单';
    },
    // 显示新增模态框
    showAdd(param) {
      if (this.$store.state.programMkis === 1) {
        this.showPickDialog = true;
        this.$refs.pickModal.findList();
      } else {
        this.showAddReturnMaterialDialog = true;
        this.$refs.addReturnMaterial.showList();
      }
    },
    // 打开打印
    showPrint(row) {
      console.log(row);
      this.$refs.printModal.showData(row);
      this.showPrintDialog = true;
    },
    // 关闭打印
    handlePrintClose() {
      this.showPrintDialog = false;
      this.showAddReturnMaterialDialog = false;
      this.showPickDialog = false;
      this.showList();
    },
    /**
     * @description: 点击取消
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    cancelAction: function() {
      // this.$refs.ruleForm.clearValidate();
      // if (this.handleCancel) {
      //   this.handleCancel();
      // }
      this.$emit('handleMkMaterilClose');
    },
    /**
     * @description: 保存
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    confirmAction: function() {
      // if (this.handleConfirm) {
      // }
      this.$refs.ruleForm.validate(valid => {
        // 若验证通过
        if (valid) {
          var data = {
            isAdd: this.isAdd,
            data: this.formUpdate.itemName
          };
          if (!this.isAdd) {
            data.id = this.data.itemId;
          }
          this.handleConfirm(data);
          // this.clearData();
        }
      });
    },
    /**
     * @description: 点击关闭
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    closeAction: function() {
      this.$refs.ruleForm.clearValidate();
      if (this.handleClose) {
        this.handleClose();
      }
    },
    delete(row) {
      // 确认是否确认作废
      this.$confirm('是否确认作废？', TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.doDelete(row);
        })
        .catch(() => { });
    },
    async doDelete(param) {
      if (this.$store.state.programMkis === 1) { // 领料单作废
        try {
          const res = await partsPickingRemovePartPinking({
            receiptId: param.receiptId
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.showList();
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      } else { // 退料单作废
        try {
          const res = await partsMaterialReturnRemovePartsMaterialReturn({
            returnId: param.returnId
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.showList();
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      }
    }
  }
};
</script>
<style  lang="less" scoped  >
/deep/ .el-dialog__body {
    padding: 0px 10px;
}
</style>
