        }
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        // {
        //   type: 'checkbox',
        //   width: '50',
        //   fixed: 'left',
        //   align: 'center'
        // },
        {
          field: 'werks',
          title: '工厂',
          width: 'auto',
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'idnrk',
          title: 'BOM 组件',
