    /**
     * @description: 获取所有维修任务
     * @param: void
     * @return: void
     */
    getFixTableData() {
      this.clickCancel();
      this.tableLoading = true;
      var self = this;
      var params = null;
      if (this.fixTableData && this.fixTableData.page) {
        params = {
          current: this.fixTableData.page.currentPage,
          size: this.fixTableData.page.pageSize
        };
      }

      var conditionJson = {
        conditionJson1: [],
        operatorGroup: 1,
        conditionJson2: []
      };
      if (this.savedSearchCondition.length > 0) {
        conditionJson = searchCondetionFunc(...this.savedSearchCondition);
      }

      var state = this.active2;
      if (this.active2 === 4) {
        state = 5;
      }

      var startArr = [];
      startArr.push(state);
      if (state === 2 && this.tabs[0] === 0) {
        startArr.push(1);
      }
      if (state === 2 && this.tabs[2] === 0) {
        startArr.push(5);
      }

      conditionJson.conditionJson1.push({
        column: 'repairState',
        operator: 1,
        ptype: 'list',
        type: 'in',
        value: startArr.join(',')
      });

      if (state === 2) {
        var isBoss = localStorage.getItem('isBoss');
        console.log('666', isBoss);
        if (isBoss === '1') {

        } else {
          conditionJson.conditionJson1.push({
            column: 'userId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: localStorage.getItem('userId')
          });
        }
      }

      selectRepairTaskRz(params, conditionJson)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            self.fixTableData.data = FixTaskService.generateFixTableData(
              res.rows
            );
            self.fixTableData.page.total = res.total;
            self.tableLoading = false;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
