    /**
     * @description: 显示确认维修modal
     * @param {Array} data 选中的数据
     * @return: void
     */
    showConfirmFixModal() {
      var data = {};

      if (!this.$refs.FixGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        data = this.$refs.FixGrid.getRadioRecord();
      }
      this.repairTaskId = data.repairTaskId;
      this.$refs.fixTaskConfirm.clearData();
      // this.$refs.fixTaskConfirm.initPage();
      // this.$refs.fixTaskConfirm.setTableColumn();
      // if (readonly) {
      //   this.fixTaskConfirmTaskModalData.title = EQUIPMENT_DATA.REPAIR_DETAIL;
      //   this.fixTaskConfirmTaskModalData.readonly = true;
      // } else {
      this.fixTaskConfirmTaskModalData.title = EQUIPMENT_DATA.REPAIR_CONFIRM;
      this.fixTaskConfirmTaskModalData.readonly = false;
      // }
      // 根据id获取维修任务详情
      getRepairTaskByRepairTaskIdRz({
        repairTaskId: data.repairTaskId
      })
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            var result = FixTaskService.generateFixTableData([res.rows]);
            var requiredData = result[0];
            requiredData.confirmRepairType = requiredData.repairTypeIdList
              ? requiredData.repairTypeIdList
              : [];
            requiredData.startTime = requiredData.startTime
              ? requiredData.startTime
              : null;
            requiredData.endTime = requiredData.endTime
              ? requiredData.endTime
              : null;
            requiredData.occurTime = this.$options.filters.formatTime(
              requiredData.occurTime
            );
            requiredData.userIdList = requiredData.repairUserIdList
              ? requiredData.repairUserIdList
              : [Number(localStorage.getItem('userId'))];
            requiredData.reasonContent = requiredData.reasonContent
              ? requiredData.reasonContent
              : null;
            this.fixTaskConfirmTaskModalData.data = requiredData;
            this.fixTaskConfirmTaskModalData.dataset = res.dataset;
            this.fixTaskConfirmTaskModalData.show = true;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
      selectRepairTypeByDeviceCode({
        deviceCode: data.deviceCode
      }).then((res) => {
        if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
          var result = {
            name: 'repairTypeData',
            data: res.rows
          };
          this.$store.commit('setFiltersData', result);
        }
      });
    },
