    /**
     * @description: 处理选中的设备类型
     * @param {Object} data 设备类型数据
     * @return: void
     */
    handleDeviceTypeSelect(data) {
      var nameList = [];
      for (var i = 0; i < data.typeIdList.length; i++) {
        for (
          var j = 0;
          j < this.$store.state.filters.RelatedDeviceType.length;
          j++
        ) {
          if (
            this.$store.state.filters.RelatedDeviceType[j].typeId ===
            data.typeIdList[i]
          ) {
            nameList.push(
              this.$store.state.filters.RelatedDeviceType[j].typeName
            );
            break;
          }
        }
      }
      data.typeNameList = nameList;
      data.typeNames = nameList.toString();
    },
    /**
     * @description: 处理选中的状态信息
     * @param {Object} data 状态信息数据
     * @return: void
     */
    handleDeviceTypeDownTimeSelect(data) {
      for (var key in FIXTASK_TYPE_STATUS) {
        if (FIXTASK_TYPE_STATUS[key] === data.downtimeValue) {
          data.downTime = key;
          break;
        }
      }
    },
